@import url(https://fonts.googleapis.com/css?family=Montserrat|Quicksand);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background-color: #5f45bb;
  background-image: linear-gradient(to bottom right, #180cac, #d054e4); color: #fff;
  font-family: "Quicksand", sans-serif;
  font-size: 16px;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  line-height: 1.5;
  min-height: 100%;
  min-width: 300px;
  overflow-x: hidden;
  text-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}

.wallpaper {
	background-image: url(/ryan-watkins/static/media/clouds.2eb277d9.jpg);
	background-position: center;
	background-size: cover;
	opacity: 0.2;
  position: fixed;
  display: block;
	height: 100%;
	left: 0;
	top: 0;
	width: 100%;
}


.App {
  text-align: center;
  position: relative;
}

.list{
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: wrap;
  margin-bottom: 200px;
}

#title {
  font-size: 3.25rem;
	line-height: 1.125;
  margin-bottom: 4rem;
  font-family: "Montserrat", "Quicksand", sans-serif; 
	letter-spacing: 0.3em;
	text-transform: uppercase;
}

.exp{
  margin-top: 300px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 100px;
  margin-bottom: 200px;
  flex-direction: column;
}

.social {
  color: currentColor;
  text-decoration: none;
  margin-bottom: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.btn {
  color: currentColor;
	cursor: pointer;
  text-decoration: none;
  position: relative;
  margin: 10px;
}

a {
	color: currentColor;
	cursor: pointer;
	text-decoration: none;
}

.picture-shadow,
.picture-image {
	display: block;
	height: 100%;
	left: 0;
	top: 0;
	width: 100%;
}

.job, .button {
	font-family: "Montserrat", "Quicksand", sans-serif; 
	letter-spacing: 0.3em;
	text-transform: uppercase;
}

.content {
	display: flex;
	position: relative;
    min-height: 100vh;
    margin-top: 0;
}

.side {
	max-height: 20rem;
	max-width: 20rem;
}

.about {
	max-width: 26rem;
}

.picture-shadow {
	border-radius: 21298px;
	background-image: radial-gradient(#000 0%, rgba(0, 0, 0, 0) 75%); 
	position: absolute;
	top: 5%;
	left: 2%;
}

.picture { 
	padding-top: 100%; 
	position: relative; 
	width: 100%;
}

.picture-image { 
	border-radius: 21298px; 
	position: absolute;
}

.name {
	font-size: 2.25rem;
	line-height: 1.125;
	margin-bottom: 0.5rem; 
}

.job {
	color: #ffe479; 
	font-size: 0.75rem;
}

.hr {
	background-color: #ff470f; 
	border: none;
	content: "";
	height: 1px;
	margin-bottom: 1.5rem; 
	margin-top: 1.5rem; 
	transform-origin: center left; 
	width: 4rem;
}

.description {
	font-size: 1.5rem; 
}

.contact {
	display: inline-block;
	margin-top: 1.5rem;
	vertical-align: top;
}


@media screen and (max-width: 799px) { 
	.content {
		flex-direction: column; 
		justify-content: center; 
		align-items: center; 
		padding: 5rem 3rem;
	}

	.side {
		margin-bottom: 3rem; 
		width: 100%;
	} 

}

@media screen and (min-width: 800px) { 
	.content {
		align-items: center; 
		justify-content: space-around; 
		justify-content: space-evenly; 
	}
	.side {
		flex-grow: 0; 
		flex-shrink: 0; 
		height: 20rem; 
		margin-left: 4rem; 
		order: 2;
		width: 20rem;
	}
	.about { 
		flex-grow: 1; 
		flex-shrink: 1;
	} 	
}
.card {
    height: 350px;
    width: 350px;
    min-width: 300px;
    min-height: 250px;
    position: relative;
    border: 1px solid black; 
    /* border-bottom: 8px groove green; */
    margin: 70px;
    background-color: rgb(255,255,244);
    border-radius: 15px;
    box-shadow: 5px 5px 10px black;
    float: left;
}

.hex {
    height: 100px;
    position: relative;
    top: -50px;
}

.card h2 {
    position: absolute;
    left: 50%;
    top: 10%;
    transform: translate(-50%);
    padding: 5px;
    color: rgb(44, 41, 41);
    text-transform: uppercase;
    font-family: "Montserrat", "Quicksand", sans-serif; 
	letter-spacing: 0.2em;
}

.card p {
    position: relative;
    color: rgb(44, 41, 41);
    margin-top: 50px;
    margin-left: 25px;
    margin-right: 25px;
    font-family: "Montserrat", "Quicksand", sans-serif; 
	letter-spacing: 0.1em;
}
.triangleUp{
    float: left;
    border-top: 52px solid transparent;
    border-bottom: 52px solid transparent;
}

.triangle-down {
    float: left;
    border-top: 52px solid transparent;
    border-bottom: 52px solid transparent;    
}

.rectangle {
    float: left;
    height: 104px;
    width: 60px;
    position: relative;
}

.center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120px;
    padding: 0;
}
.expContainer {
    display: flex;
    flex-direction: row;
    width: 150vh;
    margin: 30px;
    border-radius: 15px;
    opacity: 1.0;
}



.expContainer .company{
    flex-basis: 20%;
    background-color: rgb(178 , 226, 255);
    min-width: 150px;
    min-height: 100px;
    border-radius: 15px;
    box-shadow: 5px 5px 10px black;
    margin-right: 10px;
    text-align: center;
}

.expContainer .company h2 {
    position: relative;
    left: 50%;
    top: 25%;
    transform: translate(-50%, -50%);
    padding: 5px;
    color: rgb(44, 41, 41);
    text-transform: uppercase;
    font-family: "Montserrat", "Quicksand", sans-serif; 
    letter-spacing: 0.2em;
    font-size: 100%;
}

.expContainer .description{
    border: 1px solid black;
    min-width: 300px;
    min-height: 100px;
    width: 100%;
    background-color: rgba(255,255,244, 1);
    border-radius: 15px;
    box-shadow: 5px 5px 10px black;
    padding: 10px;
}


.expContainer .description h2 {
    position: relative;
    left: 50%;
    transform: translate(-50%);
    padding: 5px;
    color: rgb(44, 41, 41);
    text-transform: uppercase;
    font-family: "Montserrat", "Quicksand", sans-serif; 
    letter-spacing: 0.2em;
    font-size: 100%;
}

.expContainer p {
    position: relative;
    color: rgb(44, 41, 41);
    margin-top: 10px;
    margin-left: 25px;
    margin-right: 25px;
    font-family: "Montserrat", "Quicksand", sans-serif; 
    font-size: medium;
    text-align: justify;
}

